.project {
    width: 300px;
    background-color: #f8ecec;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: center;
    margin: 0;
    /*text-align: center;*/

}

.imgContainer {
    width: 100%;
    height: 200px;
    background-color: #a8a8a8;
    background-size: cover;
    background-position: center;
    display: flex;
    justify-content: center;
    align-items: center;
}

.button {
    width: 80px;
    height: 30px;
    background-color: dimgrey;

    text-decoration-line: none;
    color: white;
}

.projectInfo {
    text-align: center;
    padding: 20px;
    box-sizing: border-box;
}

.title {
    font-weight: bold;
    padding-bottom: 5px;

}

.description {
    text-align: justify-all;
}
